html {
  text-align: center;
  background: rgb(19, 25, 45);
}

body {
  color: white;
  margin: 3px !important;
  font-family: sans-serif;
}

.item-image {
  border: 2px solid white;
  margin: 0.2rem;
  width: 66px;
}

.item-image-empty {
  border-color: transparent !important;
}

code {
  display: block;
  white-space: pre-wrap;
  text-align: left;
}

.items-section {
  max-width: 420px; /* 380px for 64px item images */
  margin: 0 auto;
  margin-bottom: 2rem;
  line-height: 0;
}

.items-section .title {
  width: 100%;
  border-bottom: 1px solid white;
  color: white;
  text-transform: uppercase;
  text-align: left;
}

.navigation button {
  padding: 0.7rem;
  margin: 0.3rem;
  background-color: transparent;
  color: white;
  border: 2px solid white;
  text-transform: uppercase;
  font-weight: 600;
  cursor: pointer;
}

.item-tooltip {
  border: 1px solid rgba(255,255,255,0.4);
  width: 450px;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.item-tooltip-header {
  height: 56px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

.item-tooltip-header > img {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 56px;
  width: 56px;
  margin-left: 3px;
}

.item-tooltip-header > .info {
  -ms-flex: 9 1;
      flex: 9 1;
  padding: 0.1rem;
  padding-left: 1em;
  -ms-flex-pack: left;
      justify-content: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.item-tooltip-header > .info > span {
  line-height: normal;
  text-align: left;
}

.item-tooltip-header > .info > span:nth-of-type(1) {
  -ms-flex: 3 1;
      flex: 3 1;
  text-transform: uppercase;
  font-size: 200%;
  font-weight: bold;
}

.item-tooltip-header > .info > span:nth-of-type(2) {
  -ms-flex: 2 1;
      flex: 2 1;
  text-transform: capitalize;
  font-size: 110%;
  font-weight: 400;
}

.item-tooltip-header > * {
  float: left;
}

.item-tooltip-header > h1, .item-tooltip-header > h2 {
  display: inline-block;
  width: calc(100% - 48px);
}

.item-tooltip-header > h1 {
  margin: .25em 0 0 .75rem;
  text-transform: uppercase;
  font-size: 1.9em;
}

.item-tooltip-header > h2 {
  margin: 0 0 0 .75rem;
  text-transform: capitalize;
  font-size: 1.4em;
  font-weight: 300;
}

.item-tooltip.legendary > .item-tooltip-header {
  background-color: rgb(81,49,99);
}
.__react_component_tooltip.show {
  opacity: 1 !important;
  padding: 0px !important;
}


